class Definer {

  /**
   * 
   * @param {String} name  
   * @param {*} value  
   */
  static create(name, value) {
    return new Definer(name, value)
  }

  constructor(name, value) {
    this.configurable = true
    this.enumerable = true
    this.writable = true
    this.name = name
    this.value = value
  }

  /**
   * 
   * @param {Boolean} v 
   * @description       .
   */
  seal(v) {
    this.configurable = !v
    return this
  }

  /**
   * 
   * @param {Boolean} v 
   * @description       .
   */
  hidden(v) {
    this.enumerable = !v
    return this
  }

  /**
   * 
   * @param {Boolean} v
   * @description       . 
   */
  final(v) {
    this.writable = !v
    return this
  }

  /**
   * 
   * @param {*} obj 
   * @description    .
   */
  to(obj) {
    Object.defineProperty(obj, this.name, this)
  }

}


export default Definer